@import url(https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,400;0,600;0,700;0,800;1,400;1,500;1,600&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.animation-container {
  width: 500px;
  height: 800px;
  pointer-events: none;
  margin-right: 50px;
  margin-left: 50px;
}

.App {
  font-family: 'Montserrat', sans-serif;
  color: white;
}

.App-header {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  width: 100%;
  height: 100vh;
  background-color: #151515;
  background-attachment: fixed;
}


.quotes-container{
  /* display: flex;
  width: 800px;
  justify-content: center; */
  margin-top: 100px;
  margin-left: auto;
  margin-right: auto;
  /* margin: auto; */
  width: 75%;
}


.episode-wrapper {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-align-content: space-between;
          align-content: space-between;
  margin-top: 100px;
  color: black;
}
.landing-page-wrapper{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-content: center;
            align-content: center;
    -webkit-align-items: center;
            align-items: center;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    margin-left: 50px;
}

.description-body {
    width: 500px;
    font-size: 20px;
    text-align: left;
    margin-top: 5px;
    font-weight: 600;
}

/* Deals with bottom title on page */

.title-wrapper {
    display: -webkit-flex;
    display: flex; 
    -webkit-flex-direction: column; 
            flex-direction: column;  
}

#title {
    margin-right: 15px;
    margin-bottom: 5px;
    font-size: 96px;
    font-weight: 800;
}

.carousel {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
}

#quote-body {
    font-style: italic;
